<template>
    <div class="body">
        <div class="header">
            <van-nav-bar fixed placeholder title="文创活动" left-text="返回" @click-left="goBack" left-arrow>
               <van-icon name="search" slot="right" />
            </van-nav-bar>
        </div>
        <div class="main">
            <div class="title">
                <h2>
                    {{ activity.title }}
                </h2>
            </div>
            <div class="activity-date">
               <van-row>
                    <van-col :span="8">
                        <span>{{ adate }}</span>
                    </van-col>
                    <van-col :offset="1">
                        <span class="article-editor">编辑: {{ activity.editor }}</span>
                    </van-col>
               </van-row>
            </div>
            <div class="tags">
                <van-tag plain>{{ activity.tag1 }}</van-tag>
                &nbsp;
                <van-tag plain>{{ activity.tag2 }}</van-tag>
                &nbsp;
                <van-tag plain>{{ activity.tag3 }}</van-tag>
            </div>
            <div style="margin: 0.8rem;">
                <van-divider></van-divider>
            </div>
            <div class="article">
                <p>
                    {{ activity.p1 }}
                </p>
                <van-image
                        width="100%"
                        fit="cover"
                        :src="base + activity.img1"
                        />
                <p>
                    {{ activity.p2 }}
                </p>
                <p>
                    {{ activity.p3 }}
                </p>
                <p>
                    {{ activity.p4 }}
                </p>
                <van-image
                        width="100%"
                        fit="cover"
                        :src="base + activity.img2"
                        />
                <p>
                    {{ activity.p5 }}
                </p>
            </div>
        </div>
        <div class="footer"></div>
    </div>
</template>
<style>
.body{
    min-height: 100vh;
    background-color: #f1f5f7;
}

.header{

}

.title{
    margin: 1rem;
}

.activity-date{
    text-align: left;
    margin-left: 1rem;
}

.article-editor{
    color: gray;
}

.tags{
    text-align: left;
    margin-left: 1rem;
}

.article{
    text-align: left;
    margin: 0.5rem;
}

.main{
    margin-top: 1rem;
}

.footer{

}


</style>
<script>
import axios from 'axios';

    const options = {
        data(){
            return {
                activity: {},
                adate: "",
            }
        },
        computed: {
            date(){
                const now = new Date();
                const year = now.getFullYear();
                const month = String(now.getMonth() + 1).padStart(2, '0');
                const day = String(now.getDate()).padStart(2, '0');
                const hours = String(now.getHours()).padStart(2, '0');
                const minutes = String(now.getMinutes()).padStart(2, '0');
                
                const formattedDate = `${year}/${month}/${day}  ${hours}:${minutes}`;
                return formattedDate;
            },
            base(){
                return axios.defaults.baseURL;
            }
        },
        methods: {
            goBack(){
                this.$router.push("/index");
            },
            getActivity(){
                axios.get("/admin/getactivity").then((resp)=>{
                    this.activity = resp.data.data;
                    let d = resp.data.data.date;
                    var dateTimeString = d;
                    var date = new Date(dateTimeString);
                    // 获取年、月、日
                    var year = date.getFullYear();
                    var month = date.getMonth() + 1; // 月份从 0 开始，所以需要加 1
                    var day = date.getDate();
                    var formattedDate = year + "-" + (month < 10 ? "0" : "") + month + "-" + (day < 10 ? "0" : "") + day;
                    this.adate = formattedDate;
                });
            }
        },
        mounted(){
            this.getActivity();
        }
    };

    export default options;
</script>